<template>
  <div>
    <div class="flex justify-between">
      <vs-button class="focus:outline-none" @click="add()">Agregar Nuevo</vs-button>
      <h1 class="text-3xl text-gray-400 font-medium">Registro de Nota</h1>
    </div>

    <list :dataList="get_nota" @edit="edit" />

    <add
      :dialog="dialog"
      @changedialog="dialog = false"
      @closedialog="closeDialog()"
      :item_selected="item_selected"
    />
  </div>
</template>

<script>
import List from "./componets/List.vue";
import Add from "./componets/Add";

export default {
  components: { List, Add },

  data() {
    return {
      nota_data: [],
      dialog: false,
      item_selected: {},
    };
  },

  computed: {
    get_nota() {
      return this.$store.getters["nota/get_nota"];
    },
  },

  mounted() {
    this.$store.dispatch("nota/set_nota");
  },

  methods: {
    closeDialog() {
      this.dialog = false;
      this.item_selected = null;
    },

    add() {
      this.item_selected = null;
      this.dialog = true;
    },

    edit(e) {
      this.item_selected = e;
      if (this.item_selected) {
        this.dialog = true;
      }
    },
  },
};
</script>