<template>
  <div class="center mt-5 w-full">
    <div class=" p-5 rounded-3xl bg-gray-200 w-32">
      <span
        >Total: <b class=" text-3xl">{{ dataListLocal.length }}</b></span
      >
    </div>

    <div class="mt-5">
      <vs-button success @click="exportToExcel">
        Exportar a Excel
      </vs-button>
    </div>

    <vs-table striped class=" mt-5">
      <template #header>
        <div class=" mb-2 text-lg font-medium px-2">
          <h1>Filtros</h1>

          <div class=" w-1/12 flex items-center">
            <span for="">
              <vs-radio
                v-model="limite"
                val="true"
                @input="$emit('limits', true)"
                >50</vs-radio
              >
            </span>

            <span for="">
              <vs-radio
                v-model="limite"
                val="false"
                @input="$emit('limits', false)"
                >Todos</vs-radio
              >
            </span>
          </div>
        </div>

        <vs-input v-model="dni" border placeholder="Buscar" />
      </template>

      <template #thead>
        <vs-tr>
          <vs-th
            v-for="(item, i) in columns"
            :key="i"
            sort
            @click="
              dataListLocal = $vs.sortData($event, dataListLocal, item.name)
            "
          >
            {{ item.text }}
          </vs-th>
        </vs-tr>
      </template>

      <template #tbody>
        <vs-tr :key="i" v-for="(tr, i) in dataListLocal" :data="tr">
          <vs-td
            @click="$emit('edit', tr)"
            :edit="i2 == 0"
            v-for="(item, i2) in columns"
            :key="i2"
          >
            <span
              :class="{
                ' font-bold': item.name == 'promedio' || item.name == 'dni',
              }"
              >{{ tr[item.name] }}</span
            >
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No existen datos 
      </template>
    </vs-table>
  </div>
</template>
<script>
import axios from "@/plugins/axios";

export default {
  props: {
    dataList: {
      type: Array,
    },
  },

  data: () => ({
    columns: [
      {
        name: "id",
        text: "Id",
      },

      {
        name: "dni",
        text: "Dni",
      },

      {
        name: "nombre",
        text: "Nombre",
      },

      {
        name: "nota1",
        text: "N. 1",
      },

      {
        name: "nota2",
        text: "N. 2",
      },

      {
        name: "nota3",
        text: "N. 3",
      },

      {
        name: "nota4",
        text: "N. 4",
      },

      {
        name: "nota5",
        text: "N. 5",
      },

      {
        name: "nota6",
        text: "N. 6",
      },

      {
        name: "nota7",
        text: "N. 7",
      },

      {
        name: "nota8",
        text: "N. 8",
      },

      {
        name: "nota9",
        text: "N. 9",
      },

      {
        name: "nota10",
        text: "N. 10",
      },

      {
        name: "nota11",
        text: "N. 11",
      },

      {
        name: "nota12",
        text: "N. 12",
      },

      {
        name: "nota13",
        text: "N. 13",
      },

      {
        name: "nota14",
        text: "N. 14",
      },

      {
        name: "nota15",
        text: "N. 15",
      },

      {
        name: "nota16",
        text: "N. 16",
      },

      // {
      //   name: "ppa",
      //   text: "PPA",
      // },

      // {
      //   name: "evaluacion20",
      //   text: "Ev. 20%",
      // },

      {
        name: "promedio",
        text: "Promedio",
      },
    ],
    dataListLocal: [],
    limite: "true",
    dni: "",
  }),

  watch: {
    dataList(newValue) {
      this.dataListLocal = newValue;
    },

    limite(newValue) {
      this.$store.dispatch("nota/set_nota", { dni: this.dni, limit: newValue });
    },

    dni(newValue) {
      const buscar = newValue.length;
      if (buscar == 0 || buscar == 4 || buscar == 8) {
        this.$store.dispatch("nota/set_nota", {
          dni: newValue,
          limit: this.limite,
        });
      }
    },
  },

  methods: {
    base_photo() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/media/");
      return host;
    },

    async exportToExcel() {
      const { data } = await axios.get("getExcelNotas");
      var a = document.createElement("a");
      a.href = this.base_photo() + data.excel_file;
      a.click();
    },
  },
};
</script>
