<template>
  <vs-dialog blur prevent-close not-close not-center v-model="dialogLocal">
    <template #header>
      <h4>
        <b>Registro de Nota</b>
      </h4>
    </template>

    <div @submit.prevent class="w-full flex flex-col">
      <div>
        <span class="font-medium text-xl text-green-400">Datos de Personales</span>

        <div class="mt-5">
          <label>Documento de Identidad (DNI):</label>
          <div class="flex">
            <input
              ref="txtdni"
              v-model="dni"
              placeholder="Documento de Identidad"
              type="number"
              autofocus
              class="p-2 px-4 border-gray-200 border-2 rounded-lg block w-full focus:outline-none"
              @keypress.enter="filtrar()"
              :disabled="bloquear"
            />

            <vs-button class="outline-none" @click="filtrar()">Consultar</vs-button>
          </div>

          <div class="m-2">
            <span class="text-xl font-bold">{{ nombre }}</span>

            <div class="flex justify-center items-center text-center mt-2">
              <img v-if="photo" :src="`${base_photo}${photo}`" class="foto" />
            </div>
          </div>
        </div>
      </div>

      <div class="mt-5">
        <span class="font-medium text-xl text-green-400">Registro de Notas</span>

        <div class="border-b pb-2">
          <div class="flex items-center mt-2">
            <label class="mr-2 font-medium">Nota PPA:</label>
            <input
              v-model="nota_ppa"
              placeholder="PPA"
              type="number"
              class="p-2 px-4 border-gray-200 border-2 rounded-lg block focus:outline-none"
              ref="txtPpa"
              :disabled="bloquear"
            />
          </div>

          <div class="flex items-center mt-2">
            <label class="mr-2 font-medium">Evaluacion 20%:</label>
            <input
              v-model="evaluacion20"
              placeholder="Evaluacion 20%"
              type="number"
              class="p-2 px-4 border-gray-200 border-2 rounded-lg block focus:outline-none"
              ref="txtPpa"
              :disabled="bloquear"
            />
          </div>
        </div>

        <div class="mt-5">
          <div class="flex items-center mt-2" v-for="i in 16" :key="i">
            <label class="mr-2 font-medium">Nota {{ i }}:</label>
            <input
              :placeholder="`Nota ${i}`"
              type="number"
              class="p-2 px-4 border-gray-200 border-2 rounded-lg block focus:outline-none"
              @keypress.enter="next(i)"
              v-model="notas[i]"
              :ref="`txtnota` + i"
            />
          </div>
        </div>
      </div>
    </div>

    <template #footer>
      <div class="flex justify-between focus:outline-none">
        <div>
          <vs-button
            v-if="id && !bloquear"
            transparent
            class="focus:outline-none"
            color="danger"
            @click="dialogConfirm = true"
          >Eliminar</vs-button>
        </div>

        <div class="flex">
          <vs-button v-if="!bloquear" class="focus:outline-none" @click="save">Guardar</vs-button>
          <vs-button dark transparent @click="exit()" class="focus:outline-none">Cancelar</vs-button>
        </div>
      </div>
    </template>

    <vs-dialog width="550px" not-center blur v-model="dialogConfirm" class="z-50">
      <template #header>
        <h4 class="not-margin">
          Eliminar
          <b>Usuario</b>
        </h4>
      </template>

      <div class="con-content">
        <p>
          Esta seguro que desea eliminar el usuario?
          <br />
          <b>Esta acci贸n no se puede deshacer</b>.
        </p>
      </div>

      <template #footer>
        <div class="flex justify-between">
          <div></div>
          <div class="flex">
            <vs-button @click="remove" transparent>Si</vs-button>
            <vs-button @click="dialogConfirm = false" dark transparent>Cancelar</vs-button>
          </div>
        </div>
      </template>
    </vs-dialog>
  </vs-dialog>
</template>

<script>
import axios from "@/plugins/axios";

export default {
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },

    item_selected: {
      type: Object,
      default: () => {
        return null;
      },
    },
  },

  data() {
    return {
      dialogLocal: false,
      dialogConfirm: false,
      id: null,
      //
      dni: "",
      notas: {},
      nombre: "",
      photo: "",
      nota_ppa: 0,
      evaluacion20: 0,
      bloquear: false,
    };
  },

  computed: {
    base_photo() {
      let host =
        process.env.NODE_ENV == "development"
          ? process.env.VUE_APP_BASE_URL
          : process.env.VUE_APP_URL;

      host = host.replace("/api/v1/", "/media/");
      return host;
    },

    get_auth() {
      return this.$store.getters["auth/get_auth"];
    },
  },

  watch: {
    dialog(newValue) {
      this.dialogLocal = newValue;
      if (newValue) {
        setTimeout(() => {
          this.$refs.txtdni.focus();
        }, 10);
      } else {
        this.bloquear = false;
      }
    },

    dialogLocal(newValue) {
      if (!newValue) {
        this.$emit("changedialog", newValue);
        this.clearData();
      }
    },

    item_selected(newValue) {
      if (newValue) {
        this.bloquear = this.get_auth.group != "administrador";
        this.clearData();
        this.id = newValue.id;
        this.dni = newValue.dni;
        this.nombre = newValue.nombre;
        this.nota_ppa = newValue.ppa;
        this.evaluacion20 = newValue.evaluacion20 || 0;
        for (let index = 1; index <= 16; index++) {
          const element = newValue[`nota${index}`];
          this.notas[index] = element;
        }

        this.filtrar();
      } else {
        this.clearData();
      }
    },
  },

  methods: {
    validar_nota_dni(dni) {
      const nota = this.$store.getters["nota/get_nota_by_dni"](dni);
      if (!nota) return true;
      if (Object.entries(nota).length <= 0 || this.id) return true;

      this.$vs.notification({
        title: "Notas",
        text: `Las notas del postulante con el dni ${dni} ya se registraron! `,
        color: "danger",
      });
      return false;
    },

    next(index) {
      if (index == 16) {
        this.save();
        return;
      }

      setTimeout(() => {
        this.$refs[`txtnota` + (index + 1)][0].focus();
      }, 10);
    },

    async filtrar() {
      if (!this.validar_nota_dni(this.dni)) {
        return;
      }
      var data = [];

      await this.$store
        .dispatch("postulante/set_postulante_by_dni", this.dni)
        .then((res) => {
          data = res;
          //

          this.dni = this.dni || "";

          if (data && this.dni.length > 0) {
            data = data[0];
            this.nombre = `${data.apellido_paterno} ${data.apellido_materno} ${data.nombre}`;
            this.photo = data.photo;
            this.$refs[`txtnota1`][0].focus();
          }
        });
    },

    clearData() {
      this.notas = {};
      this.id = null;
      this.dni = null;
      this.nombre = "";
      this.photo = "";
      this.nota_ppa = 0;
      this.evaluacion20 = 0;
    },

    async remove() {
      const loading = this.$vs.loading();
      this.dialogConfirm = false;

      try {
        const { status } = await axios.delete(`nota/${this.id}/`);

        if (status != 204) {
          this.$vs.notification({
            title: "Notas",
            text: "No se pudo eliminar, intentelo denuevo! ",
            color: "warning",
          });
          return;
        }

        this.$vs.notification({
          title: "Notas",
          text: "Nota eliminada correctamente! ",
          color: "success",
        });

        this.exit();
      } catch (error) {
        this.$vs.notification({
          title: "Notas",
          text: "No se pudo eliminar, debido a un error en el servidor! ",
          color: "danger",
        });
      } finally {
        loading.close();
      }
    },

    validar() {
      try {
        let salida = true;
        if (this.dni.length < 8) {
          salida = false;
        }
        this.nota_ppa = this.nota_ppa || 0;

        if (this.nota_ppa.length == 0 || this.evaluacion20.length == 0) {
          salida = false;
        }

        if (salida) {
          // if (
          //   !(parseInt(this.nota_ppa) >= 0 && parseInt(this.nota_ppa) <= 20)
          // ) {
          //   salida = false;
          //   alert("La nota del ppa, debe estar entre 1 y 20");
          // }

          // if (
          //   !(
          //     parseInt(this.evaluacion20) >= 1 &&
          //     parseInt(this.evaluacion20) <= 200
          //   )
          // ) {
          //   salida = false;
          //   alert("La nota de evaluacion 20%, debe estar entre 1 y 200");
          // }
        }

        for (let index = 1; index <= 16; index++) {
          const element = this.notas[index];
          if (!element) {
            this.notas[index] = 0;
          } else {
            if (
              element.toString().length > 2 ||
              parseInt(element) > 20 ||
              parseInt(element) < 0
            )
              salida = false;
          }
        }

        if (!salida)
          this.$vs.notification({
            title: "Notas",
            text: "Por favor, verifique los campos! ",
            color: "warning",
          });

        return salida;
      } catch (error) {
        this.$vs.notification({
          title: "Notas",
          text: "Por favor, verifique los campos! ",
          color: "danger",
        });
        return false;
      }
    },

    exit() {
      this.$emit("closedialog");
      this.dialogLocal = false;
      this.clearData();
    },

    async save() {
      const loading = this.$vs.loading();
      try {
        if (!this.validar()) return;

        if (!this.validar_nota_dni(this.dni)) {
          return;
        }

        let obj = {};
        let count = 0;
        let sum = 0;
        let promedio = 0;

        for (let index = 1; index <= 16; index++) {
          const element = parseInt(this.notas[index]);
          obj[`nota${index}`] = element;
          if (element > 0) {
            count++;
            sum += element;
          }
        }
        promedio = sum / count;

        let result = [];

        obj.dni = this.dni;
        obj.promedio = parseFloat(promedio.toFixed(3)) || 0;
        obj.nombre = this.nombre;
        obj.ppa = this.nota_ppa || 0;
        obj.evaluacion20 = parseFloat(this.evaluacion20) || 0;
        obj.created_user = this.get_auth.username;

        if (!this.id) {
          await this.$store.dispatch("nota/add_nota", [obj]).then((res) => {
            result = res;
          });
        } else {
          obj.modified_user = this.get_auth.username;
          obj.id = this.id;

          await this.$store.dispatch("nota/update_nota", [obj]).then((res) => {
            result = res;
          });
        }

        if (!result) {
          this.$vs.notification({
            title: "Nota",
            text: "No se pudo realizar la acci贸n, vuelve a intentarlo! ",
            color: "danger",
          });
          return;
        }

        this.$vs.notification({
          title: "Nota",
          text: "Acci贸n completada correctamente! ",
          color: "success",
        });

        this.exit();
      } catch (error) {
        console.error(error);
      } finally {
        loading.close();
      }
    },
  },
};
</script>
